import React, { useState, useEffect } from 'react';
import { motion, useInView } from 'motion/react';
import { transition } from '@chakra-ui/react';
import { delay } from 'motion';

const AnimatedText = () => {
    const [isVisible, setIsVisible] = useState(false);
    const [animationPhase, setAnimationPhase] = useState('initial');

    // Ref para o elemento que vamos observar
    const ref = React.useRef(null);
    const inView = useInView(ref, {
        threshold: 0.5, // Trigger quando 50% do elemento estiver visível
        once: true // Só roda uma vez
    });

    useEffect(() => {
        if (inView) {
            setIsVisible(true);
            // Sequência de animações
            setTimeout(() => setAnimationPhase('scaling'), 1000);
            setTimeout(() => setAnimationPhase('sliding'), 2000);
            setTimeout(() => setAnimationPhase('revealing'), 2500); // Começa junto com o sliding
        }
    }, [inView]);

    // Variantes para a palavra "Pronto"
    const prontoVariants = {
        initial: {
            opacity: 0,
            scale: 0.1,
            x: 500, // Começa mais à direita
            y: 0,
        },
        scaling: {
            opacity: 1,
            scale: 1,
            x: 500,
            y: 0,
            transition: {
                duration: 0.8,
                type: 'spring',
                stiffness: 200,
                damping: 10,
                //ease: "easeOut"
            }
        },
        sliding: {
            opacity: 1,
            scale: 1,
            x: 0, // Move para posição inicial do texto e permanece lá
            y: 0,
            transition: {
                duration: 2.6, // Aumentado para 1.5 segundos
                type: 'spring',
                stiffness: 7,
                damping: 4,
                //ease: "easeInOut"
            }
        },
        revealing: {
            opacity: 1,
            scale: 1,
            x: 0, // Mantém na posição durante a revelação
            y: 0,
        }
    };

    // Variantes para o texto sendo revelado (da direita para esquerda)
    const textRevealVariants = {
        initial: {
            clipPath: "inset(0 0% 0 100%)", // Esconde tudo à esquerda
        },
        revealing: {
            clipPath: "inset(0 0% 0 0%)", // Revela completamente
            transition: {
                duration: 1.5,
                ease: "easeOut"
            }
        }
    };

    // Variantes para cada palavra do resto do texto (invertendo a ordem para revelação da direita para esquerda)
    const wordVariants = {
        initial: {
            opacity: 0,
            x: 20 // Vem da direita
        },
        revealing: (i) => ({
            opacity: 1,
            x: 0,
            transition: {
                delay: (restOfText.length - 1 - i) * 0.1, // Inverte o delay para começar da direita
                duration: 1.0,
                ease: "easeOut"
            }
        })
    };

    const restOfText = ["para", "iniciar", "sua", "carreira", "musical?"];

    return (

        <div className="ml-[18px] _max-w-4xl _mx-auto">
            {/* Texto principal */}
            <div ref={ref} className="relative">
                <h1 className="text-3xl md:text-4xl font-bold _text-white leading-tight">
                    {/* Container para a palavra "Pronto" */}
                    <div className="relative inline-block top-[-5px] z-10">
                        <motion.span
                            className="inline-block _text-yellow-400 _bg-[#e5e7eb]"
                            variants={prontoVariants}
                            initial="initial"
                            animate={animationPhase}
                        >
                            Pronto
                        </motion.span>
                    </div>

                    {/* Container para o resto do texto */}
                    <motion.div
                        className="inline-block ml-3 relative overflow-hidden"
                        variants={textRevealVariants}
                        initial="initial"
                        animate={animationPhase === 'sliding' || animationPhase === 'revealing' ? 'revealing' : 'initial'}
                    >
                        <div className="flex flex-wrap">
                            {restOfText.map((word, index) => (
                                <motion.span
                                    key={index}
                                    className="inline-block mr-3"
                                    variants={wordVariants}
                                    initial="initial"
                                    animate={animationPhase === 'sliding' || animationPhase === 'revealing' ? 'revealing' : 'initial'}
                                    custom={index}
                                >
                                    {word}
                                </motion.span>
                            ))}
                        </div>
                    </motion.div>
                </h1>

            </div>

        </div>

    );
};

export default AnimatedText;