import React, { useState } from 'react';
import { motion, AnimatePresence } from 'motion/react';

const SoundEmanatingButton = () => {
    const [isHovered, setIsHovered] = useState(false);
    const [animationKey, setAnimationKey] = useState(0);

    const handleMouseEnter = () => {
        setIsHovered(true);
        setAnimationKey(prev => prev + 1); // Força nova animação a cada hover
    };

    const handleMouseLeave = () => {
        setIsHovered(false);
    };

    // Configurações das notas musicais - animação mais rápida
    const notes = [
        { symbol: '♪', color: 'text-blue-400', direction: { x: 60, y: -90 }, delay: 0 },
        { symbol: '♫', color: 'text-blue-300', direction: { x: 0, y: -130 }, delay: 0.05 },
        { symbol: '♪', color: 'text-blue-400', direction: { x: -60, y: -100 }, delay: 0.1 },
        { symbol: '♬', color: 'text-blue-400', direction: { x: 80, y: -120 }, delay: 0.15 },
        { symbol: '♪', color: 'text-blue-300', direction: { x: 80, y: 50 }, delay: 0.2 },
        { symbol: '♫', color: 'text-blue-400', direction: { x: 60, y: 90 }, delay: 0.25 },
        { symbol: '♪', color: 'text-blue-400', direction: { x: 0, y: 80 }, delay: 0.3 },
        { symbol: '♬', color: 'text-blue-300', direction: { x: -80, y: -110 }, delay: 0.35 },
        { symbol: '♪', color: 'text-blue-400', direction: { x: -80, y: 50 }, delay: 0.4 },
        { symbol: '♫', color: 'text-blue-400', direction: { x: -80, y: -20 }, delay: 0.45 }
    ];

    // Configurações das ondas - intervalos maiores entre elas
    const leftWaves = [
        { size: 'w-[30px] h-[50px]', delay: 0 },
        { size: 'w-[30px] h-[50px]', delay: 0.3 },
        { size: 'w-[30px] h-[50px]', delay: 0.6 }
    ];

    const rightWaves = [
        { size: 'w-[30px] h-[50px]', delay: 0 },
        { size: 'w-[30px] h-[50px]', delay: 0.3 },
        { size: 'w-[30px] h-[50px]', delay: 0.6 }
    ];

    // Estado para controlar se a animação inicial já foi executada
    const [hasAnimatedIn, setHasAnimatedIn] = useState(false);

    // Detecta quando o botão entra na viewport
    React.useEffect(() => {
        const button = document.getElementById('btn-animado-1');
        if (!button) return;
        const observer = new window.IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    setHasAnimatedIn(true);
                    observer.disconnect();
                }
            },
            { threshold: 0.5 }
        );
        observer.observe(button);
        return () => observer.disconnect();
    }, []);

    return (
        <div className="relative">
            {/* Container para isolar o efeito de escala do botão */}
            <div className="relative">
                {/* Botão Principal com Bounce Effect - Isolado */}
                <motion.button
                    id="btn-animado-1"
                    className="relative px-12 py-6 bg-gradient-to-r from-cyan-800 to-blue-800 text-white font-bold text-lg rounded-full shadow-2xl z-10"
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}
                    initial={{
                        top: 150,
                        opacity: 0,
                        borderRadius: '100%',
                        scale: 0.5
                    }}
                    animate={hasAnimatedIn ? {
                        top: 0,
                        opacity: 1,
                        borderRadius: '50px', // Mantém circular
                        scale: 1
                    } : {}}
                    transition={{
                        type: 'spring',
                        stiffness: 200,
                        damping: 10,
                        duration: 1.0,
                        top: {delay: 6},
                        opacity: {delay: 6},
                        borderRadius: {delay: 6},
                    }}
                    whileHover={{
                        scale: 1.15,
                        boxShadow: "0 25px 50px -12px rgba(6, 182, 212, 0.5)"
                    }}
                >
                    <motion.span
                        initial={{ opacity: 0 }}
                        animate={hasAnimatedIn ? { opacity: 1 } : { opacity: 0 }}
                        transition={{ delay: 1.0, duration: 0.3 }}
                        style={{ visibility: hasAnimatedIn ? 'visible' : 'hidden' }}
                    >
                        Eu quero começar
                    </motion.span>
                </motion.button>

                {/* Container para ondas e notas - Fora do botão que escala */}
                <div className="absolute inset-0">
                    {/* Ondas Sonoras Curvas - Lado Esquerdo */}
                    <div className="absolute top-[20%] -translate-y-1/2 -left-10">
                        <AnimatePresence>
                            {isHovered && leftWaves.map((wave, index) => (
                                <motion.div
                                    key={`left-${animationKey}-${index}`}
                                    className={`absolute ${wave.size} border-l-8 border-cyan-300`}
                                    style={{
                                        top: '50%',
                                        transform: 'translateY(-50%)',
                                        borderRadius: '50% 0 0 50%' // Mais arredondado
                                    }}
                                    initial={{
                                        x: 0,
                                        scale: 1,
                                        opacity: 0
                                    }}
                                    animate={{
                                        x: -40,
                                        scale: 1.8,
                                        opacity: [0, 0.8, 0]
                                    }}
                                    transition={{
                                        duration: 0.6, // Mais rápido
                                        delay: wave.delay,
                                        ease: "linear"
                                    }}
                                />
                            ))}
                        </AnimatePresence>
                    </div>

                    {/* Ondas Sonoras Curvas - Lado Direito */}
                    <div className="absolute top-[20%] -translate-y-1/2 -right-10">
                        <AnimatePresence>
                            {isHovered && rightWaves.map((wave, index) => (
                                <motion.div
                                    key={`right-${animationKey}-${index}`}
                                    className={`absolute ${wave.size} border-r-8 border-blue-300`}
                                    style={{
                                        top: '50%',
                                        transform: 'translateY(-50%)',
                                        borderRadius: '0 50% 50% 0' // Mais arredondado
                                    }}
                                    initial={{
                                        x: 0,
                                        scale: 1,
                                        opacity: 0
                                    }}
                                    animate={{
                                        x: 60,
                                        scale: 1.8,
                                        opacity: [0, 0.8, 0]
                                    }}
                                    transition={{
                                        duration: 0.6, // Mais rápido
                                        delay: wave.delay,
                                        ease: "linear"
                                    }}
                                />
                            ))}
                        </AnimatePresence>
                    </div>

                    {/* Notas Musicais Emanando */}
                    <div className="absolute inset-0 overflow-visible pointer-events-none">
                        <AnimatePresence>
                            {isHovered && notes.map((note, index) => (
                                <motion.span
                                    key={`note-${animationKey}-${index}`}
                                    className={`absolute text-2xl ${note.color}`}
                                    style={{
                                        top: '50%',
                                        left: '50%'
                                    }}
                                    initial={{
                                        x: note.direction.x > 0 ? 40 : note.direction.x < 0 ? -40 : 0,
                                        y: note.direction.y > 0 ? 40 : note.direction.y < 0 ? -60 : 0,
                                        scale: 1,
                                        opacity: 0
                                    }}
                                    animate={{
                                        x: note.direction.x,
                                        y: note.direction.y,
                                        scale: 2,
                                        opacity: [0, 1, 0]
                                    }}
                                    transition={{
                                        duration: 1.0, // Mais rápido
                                        delay: note.delay,
                                        ease: "easeOut"
                                    }}
                                >
                                    {note.symbol}
                                </motion.span>
                            ))}
                        </AnimatePresence>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default SoundEmanatingButton;